













































import { computed, defineComponent, PropType } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { SocialIconListData } from '@/app/shared/client/type/social-icon-list';
import { socialIconLabel } from '@/app/shared/client/helper/social-icon-list';

const SocialIconListContentEditor = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<SocialIconListData>,
    },
  },
  setup(props, { root }) {
    const i18n = useI18N(root.$store);

    const socialIconList = computed(() => props.value?.list ?? []);
    const socialIconListButtonText = computed(() => {
      const socialIconLabelList = socialIconList.value
        .map((socialIcon) => i18n(socialIconLabel(socialIcon.type)));
      return socialIconLabelList.join(', ');
    });
    const atLeastOneSocialIcon = computed(() => socialIconList.value.length > 0);

    return {
      i18n,
      socialIconListButtonText,
      atLeastOneSocialIcon,
    };
  },
});

export default SocialIconListContentEditor;
